<script>
// @ is an alias to /src
// import HelloWorld from "@/components/HelloWorld.vue";
import "@/lib/loaders.min.css";
import { getTopoShow } from "@/api";

export default {
  name: "Home",
  components: {
    // HelloWorld,
  },
  data() {
    return { 
      loading: true,
      vmc_num: 0,
      cpu_num: 0,
      rtu_num: 0,
      switch_num: 0,
    };
  },
  methods: {
    async getNameOAll() {
      const { data } = await getTopoShow();
      if (data.code == 0) {
        this.vmc_num = 0;
        this.cpu_num = 0;
        this.rtu_num = 0;
        this.switch_num = 0;
        this.vmcs = data.data.node
          .map((item) => {
            // return item.device_type == "vmc" || item.device_type == "cpu";
            if (item.device_type == "vmc") {
              this.vmc_num += 1;
            } else if (item.device_type == "sw") {
              this.switch_num += 1;
            } else if (item.device_type == "rtu") {
              this.rtu_num += 1;
            } else {
              this.cpu_num += item.other_info[0].value.length;
            }
            return item;
          });
        // console.log(this.vmcs);
      }
    }
  },
  mounted() {
    let that = this;
    setTimeout(() => {
      that.loading = false;
      that.getNameOAll();
    }, 3000);
  },

  created() {},
};
</script>
